import React, { useState } from 'react';
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';
import Button from '@material-ui/core/Button';
import Tooltip from '@material-ui/core/Tooltip';

const ToDoLists = () => {
   const [count, setCount] = useState(0);
  const incCount = () =>{
    setCount(count+1);
  };

  const decCount = () =>{
    if(count > 0){
    setCount(count-1);
    }else{
      alert('cannot decrease below zero');
      setCount(0);
    }
  };

  return(
    <>
      <div className="main_div">
        <div className="center_div">
  <h1>{count}</h1><br />
          <div className="btn_div">
          <Tooltip title="Add"><Button onClick={incCount} className="btn-green"><AddIcon /></Button></Tooltip>
          <Tooltip title="Subtract"><Button onClick={decCount} className="btn-red"><RemoveIcon /></Button></Tooltip>
          </div>

        </div>
        

      </div>
    </>
  );

}

export default ToDoLists;